import React, { useEffect, useState } from 'react';

import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { Select } from 'antd';

import seladHorseImg from '../../../../assets/map/seladHorse.png';

import {
  FaAngleUp,
  FaAngleDown,
  FaAngleRight,
  FaAngleLeft,
  FaRedoAlt,
  FaPen,
} from 'react-icons/fa';

import {
  RouteContainer,
  RouteContraction,
  RouteContent,
  Table,
  Routes,
  RoutesNumbersContainer,
  FinalizeRoute,
  RoutesNumbersDrag,
  RouteStops,
  ActionButtonsWrapper,
  ScriptWrapper,
  TagColor,
  HorseImg,
} from './styles';
import { toast } from 'react-toastify';
import api from '../../../../services/api';

import {
  Invoice,
  RouteAmounts,
  RouteController,
  RouteData,
  Trucks,
} from '../../../../Interfaces/MapInterface';

interface RouteScripting {
  trucks: Trucks[];
  routes: Invoice[];
  routeAmounts: RouteAmounts;
  waypointTrigger?: boolean;
  setWaypointTrigger: Function;
  setDirections: Function;
  contract: boolean;
  color: string;
  setCurrentRouteController: Function;
  currentRouteController: RouteController;
  indexController: number;
  currentOrderRoutes: RouteData[];
  setCurrentOrderRoutes: Function;
}

export const RouteScpripting: React.FC<RouteScripting> = ({
  trucks,
  routes,
  routeAmounts,
  waypointTrigger,
  setWaypointTrigger,
  setDirections,
  contract,
  color,
  setCurrentRouteController,
  indexController,
  currentRouteController,
  currentOrderRoutes,
  setCurrentOrderRoutes,
}) => {
  const { Option } = Select;

  const [contractRoutes, setContractRoutes] = useState(false);

  const [truck, setTruck] = useState<string>('');

  const [loadingSave, setLoadingSave] = useState<boolean>(false);

  const [localID, setLocalID] = useState<number>();

  const [checkedRoute, setCheckedRoute] = useState<boolean>(false);

  useEffect(() => {
    setLocalID(indexController);
  }, []);

  useEffect(() => {
    routes.length > 1 && handleDirection();
  }, [waypointTrigger]);

  const handleOnDragEnd = (result: any) => {
    if (!result.destination) return;

    const items = Array.from(routes);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);

    const updatedOrder = currentOrderRoutes[currentRouteController.index];
    updatedOrder.invoices = items;
    setCurrentOrderRoutes(updatedArray(updatedOrder));

    setWaypointTrigger(!waypointTrigger);
  };

  const updatedArray = (
    updatedRoute: RouteData,
    currentIndex = currentRouteController.index,
  ) => {
    const updated = currentOrderRoutes.map((item, index) => {
      return index === currentIndex ? (item = updatedRoute) : item;
    });

    return updated;
  };

  const handleTruckSelection = (value: any) => {
    setTruck(value);
  };

  const renderTruckOptions = () => {
    return trucks.map(({ plate, id }, index) => {
      return (
        <Option value={id} key={index}>
          {plate}
        </Option>
      );
    });
  };

  const renderRoutePoints = () => {
    return (
      routes.length > 0 &&
      routes.map((item, index) => {
        return (
          <Draggable key={index} draggableId={String(index)} index={index}>
            {(provided) => (
              <div
                ref={provided.innerRef}
                {...provided.draggableProps}
                {...provided.dragHandleProps}
              >
                <span className="partTwoNumbers">
                  {item?.loading_tag === '0' ? (
                    <p>
                      <HorseImg
                        id="pocoto"
                        src={seladHorseImg}
                        alt="Sede Selad"
                        background={color}
                      />
                    </p>
                  ) : (
                    <p>{item?.loading_tag}</p>
                  )}
                </span>
              </div>
            )}
          </Draggable>
        );
      })
    );
  };

  const handleDirection = () => {
    let start = `${routes[0].latitude + ',' + routes[0].longitude}`;

    let end = `${
      routes[routes.length - 1].latitude +
      ',' +
      routes[routes.length - 1].longitude
    }`;

    let waypoints: any[] = [];

    routes.forEach((item, index) => {
      if (index !== 0 && index !== routes.length - 1) {
        const coordinates = {
          location: `${item.latitude + ',' + item.longitude}`,
        };
        waypoints.push(coordinates);
      }
    });

    let request = {
      origin: start,
      destination: end,
      travelMode: 'DRIVING',
      waypoints: waypoints,
    };

    setDirections(request);

    setCheckedRoute(true);
  };

  const disableSaveButton = () => {
    if (!truck || routes.length < 2 || loadingSave || !checkedRoute) {
      return true;
    }
    return false;
  };

  const saveLoadingRoute = async () => {
    setLoadingSave(true);

    let invoices: any[] = [];

    routes.forEach((item, index) => {
      const invoice = {
        invoice_id: item.id,
        order: String(index + 1),
        delivery_time: '2021-09-10T15:25:12-03:00',
      };

      invoices.push(invoice);
    });

    const data = {
      truck_id: truck,
      comments: 'selad',
      cubage: String(routeAmounts.cubage),
      distance: routeAmounts.distance,
      time: routeAmounts.duration,
      weight: String(routeAmounts.weight),
      invoices: invoices,
    };

    try {
      const response = await api.post(`truck-routes`, data);

      if (response) {
        toast.success('Cadastrado com sucesso');
      }
    } catch (error: any) {
      handleError(error.response?.data?.message || error.toString());
    } finally {
      setLoadingSave(false);
    }
  };

  const handleError = (error: string) => {
    toast.error(error);
  };

  const handleCurrent = () => {
    const current = {
      index: localID,
      color: color,
    };

    setCurrentRouteController(current);
  };

  return (
    <>
      <ScriptWrapper
        onClick={() => handleCurrent()}
        active={currentRouteController.index === localID ? true : false}
      >
        <div className="routeTable">
          <RouteContent>
            {contract ? (
              <Table>
                <thead>
                  <tr>
                    <th>Rota</th>

                    <th>Veculo</th>

                    <th>
                      Servios
                      <br />
                    </th>

                    <th>
                      Distncia (Km) <br />
                    </th>

                    <th>
                      Tempo(h) <br />
                    </th>

                    <th>
                      Peso(kg) <br />
                    </th>

                    <th>
                      Volume(m) <br />
                    </th>
                  </tr>
                </thead>

                <tbody>
                  <tr>
                    <TagColor
                      data-label={`Rotas(${routes.length})`}
                      color={color}
                    >
                      <div>
                        <span />

                        <div></div>
                      </div>
                    </TagColor>

                    <td data-label="Veculo">
                      <Select
                        bordered={false}
                        onChange={handleTruckSelection}
                        placeholder="Caminho"
                      >
                        {renderTruckOptions()}
                      </Select>
                    </td>

                    <td data-label="Servios">
                      <span>{routeAmounts.services}</span>
                    </td>

                    <td data-label="Distncia (Km)">{routeAmounts.distance}</td>

                    <td data-label="Tempo(h)">{routeAmounts.duration}</td>

                    <td data-label="Peso">{routeAmounts?.weight}</td>

                    <td data-label="Volume(m)">{routeAmounts.cubage}</td>
                  </tr>
                </tbody>
              </Table>
            ) : (
              ''
            )}
          </RouteContent>
        </div>

        {contract ? (
          <Routes contractRoutes={contractRoutes}>
            <RoutesNumbersContainer>
              {/* <div
                  className="routesContract"
                  onClick={() => setContractRoutes(!contractRoutes)}
                >
                  {contractRoutes ? <FaAngleLeft /> : <FaAngleRight />}
                </div> */}

              {!contractRoutes ? (
                <div className="routesNumbersDragDrop">
                  <FinalizeRoute>
                    {/* <h3>Finalizar rota:</h3> */}

                    <button
                      disabled={routes.length <= 1 && true}
                      onClick={() => handleDirection()}
                      style={{
                        backgroundColor: 'var(--blue)',
                        margin: '0 5px',
                      }}
                    >
                      Visualizar Rota
                    </button>

                    <button
                      onClick={() => saveLoadingRoute()}
                      disabled={disableSaveButton()}
                    >
                      {loadingSave ? 'Salvando' : 'Salvar'}
                    </button>
                  </FinalizeRoute>

                  <RoutesNumbersDrag color={color}>
                    <div>
                      <span className="partOne">
                        <p>Rota</p>
                      </span>

                      <span className="partTwo">
                        <DragDropContext onDragEnd={handleOnDragEnd}>
                          <Droppable
                            droppableId="dropable-container"
                            direction="horizontal"
                          >
                            {(provided) => (
                              <div
                                {...provided.droppableProps}
                                ref={provided.innerRef}
                              >
                                {renderRoutePoints()}
                                {provided.placeholder}
                              </div>
                            )}
                          </Droppable>
                        </DragDropContext>
                      </span>

                      <span className="partThree">
                        <img src={seladHorseImg} />
                      </span>
                    </div>
                  </RoutesNumbersDrag>
                </div>
              ) : (
                ''
              )}
            </RoutesNumbersContainer>
          </Routes>
        ) : (
          ''
        )}
      </ScriptWrapper>

      {contract ? (
        <RouteStops>
          <div>
            {/* <span>
                12 paradas - 10/02/2020 0.... <FaPen color="#0440A0" />
              </span>

              <span>15/01/2021 16:16 </span>

              <span>Atribudos: 9/12</span>

              <span>Rotas: 3</span>

              <span>Distncia: 741,99km</span> */}
          </div>
        </RouteStops>
      ) : (
        ''
      )}
    </>
  );
};
