import { useState, useEffect } from 'react';

import { toast } from 'react-toastify';

import {
  FaAngleUp,
  FaAngleDown,
  FaAngleRight,
  FaAngleLeft,
  FaRedoAlt,
  FaPen,
} from 'react-icons/fa';

import api from '../../services/api';

import { colors } from '../../utils/colors';

import { Header } from '../../components/Header';
import { Sidebar } from './components/Sidebar/index';

import Map from './components/Map/Map';
import Loader from './components/Map/Loader';

import './components/Map/index.css';
import { RouteScpripting } from './components/RoutesScripting';
import { PreviousRouteScpripting } from './components/PreviousRouteScripting';

import {
  NewRouteButton,
  NewRouteContainer,
  RouteContainer,
  RouteContraction,
  RouteInner,
} from './styles';

import {
  Trucks,
  Invoice,
  RouteController,
  RouteData,
  PreviousRoutes,
  DirectionsRequest,
} from '../../Interfaces/MapInterface';

export const MapLoading = () => {
  const mockedInvoices: Invoice[] = [
    {
      id: 'b24ff4c5-e300-44c0-819e-344e61715625',
      invoice_number: '003',
      order_number: '003',
      client_name: 'Clientsom Tres',
      cubage: '0',
      amount_boxes: 20,
      invoice_was_received: true,
      cep: '07400000',
      address: 'Rua dos testes',
      longitude: -46.3072047,
      latitude: -23.3374337,
      status: 'Autorizado',
      was_delivered: false,
      position_occupied: false,
      position_id: null,
      delivery_time: null,
      delivery_order: 0,
      user_id: 'aeba1881-a87c-4eb1-84ed-bc58d26944a2',
      truck_id: null,
      driver_id: null,
      current_status: null,
      status_id: null,
      created_at: '2021-08-31T19:26:32.823Z',
      invoice_route_id: null,
      updated_at: '2021-08-31T19:26:55.972Z',
    },
    {
      id: '71cb41f5-597a-4190-8f3d-4272613bff7f',
      invoice_number: '298218',
      order_number: '123458',
      client_name: 'Selad',
      cubage: '3',
      amount_boxes: 6,
      invoice_was_received: true,
      cep: '17560246',
      address: 'Avenida Paulista, Centro So Paulo -SP',
      longitude: -46.456402,
      latitude: -23.431502,
      status: 'Autorizado',
      was_delivered: false,
      position_occupied: false,
      position_id: null,
      delivery_time: null,
      delivery_order: 0,
      user_id: 'aeba1881-a87c-4eb1-84ed-bc58d26944a2',
      truck_id: null,
      driver_id: null,
      current_status: null,
      status_id: null,
      created_at: '2021-08-25T23:15:17.504Z',
      invoice_route_id: null,
      updated_at: '2021-08-26T16:57:39.509Z',
    },
  ];

  const [contract, setContract] = useState<boolean>(false);

  const [loading, setLoading] = useState(false);

  const [availableInvoices, setAvailableInvoices] = useState<Invoice[]>([]);

  const [previousRoutes, setPreviousRoutes] = useState<
    PreviousRoutes[] | undefined
  >();

  const [currentOrderRoutes, setCurrentOrderRoutes] = useState<RouteData[]>([]);

  const [trucks, setTrucks] = useState<Trucks[]>([]);

  const [directions, setDirections] = useState<DirectionsRequest>();

  const [waypointTrigger, setWaypointTrigger] = useState<boolean>(false);

  const [currentRouteController, setCurrentRouteController] =
    useState<RouteController>({
      index: 0,
      color: '',
    });

  useEffect(() => {
    fetchTrucks();

    // fetchAllInvoices();
    fetchAvailableInvoicesModel();
  }, []);

  const fetchAvailableInvoicesModel = async () => {
    setAvailableInvoices(mockedInvoices);
    fetchPreviousRoute();

    try {
      const response = await api.get(`invoices/available-delivery`);
      if (response) {
        setAvailableInvoices(response.data);
        fetchPreviousRoute();
      }
    } catch (error: any) {
      handleError(error.response?.data?.message || error.toString());
    }
  };

  const fetchPreviousRoute = async () => {
    try {
      const response = await api.get(`truck-routes?is_finished=false`);
      if (response) {
        setPreviousRoutes(response.data);
      }
    } catch (error: any) {
      handleError(error.response?.data?.message || error.toString());
    }
  };

  const fetchInvoicesInArea = async (area: any[]) => {
    const areaData = {
      coordinates: [area],
    };
    try {
      const response = await api.post(`invoices/available/load`, areaData);

      if (response) {
        setAvailableInvoices(response.data);
      }
    } catch (error: any) {
      handleError(error.response?.data?.message || error.toString());
    }
  };

  const handleError = async (error: string) => {
    toast.error(error);
  };

  const eraseData = () => {
    setAvailableInvoices([]);
  };

  const addNewOrder = () => {
    const routeData = {
      index: currentOrderRoutes.length,
      color:
        colors[
          (previousRoutes ? previousRoutes.length : 0) +
            currentOrderRoutes.length
        ],
      amounts: {
        services: 0,
        weight: 0,
        cubage: 0,
        distance: '--',
        duration: '--',
      },
      invoices: [],
    };

    setCurrentRouteController({
      ...currentRouteController,
      index: routeData.index,
      color: routeData.color,
    });
    setCurrentOrderRoutes([...currentOrderRoutes, routeData]);
  };

  const fetchTrucks = async () => {
    setLoading(true);
    try {
      const response = await api.get(`trucks?limit=9999`);

      if (response) {
        setTrucks(response.data.results);
      }
    } catch (error: any) {
      handleError(error.response?.data?.message || error.toString());
    } finally {
      setLoading(false);
    }
  };

  const renderCurrentOrderRoutesScripting = () => {
    return currentOrderRoutes.map((orderRoutes, index) => {
      return (
        <RouteScpripting
          routes={orderRoutes.invoices}
          currentOrderRoutes={currentOrderRoutes}
          setCurrentOrderRoutes={setCurrentOrderRoutes}
          routeAmounts={orderRoutes.amounts}
          color={orderRoutes.color}
          trucks={trucks}
          waypointTrigger={waypointTrigger}
          setWaypointTrigger={setWaypointTrigger}
          setDirections={setDirections}
          contract={contract}
          key={index}
          setCurrentRouteController={setCurrentRouteController}
          currentRouteController={currentRouteController}
          indexController={currentOrderRoutes.length - 1}
        />
      );
    });
  };

  const renderPreviousOrderRoutesScripting = () => {
    if (previousRoutes) {
      return previousRoutes.map(({ invoices, truck }, index) => {
        return (
          <PreviousRouteScpripting
            truck={truck}
            routes={invoices}
            color={colors[index]}
            waypointTrigger={waypointTrigger}
            setWaypointTrigger={setWaypointTrigger}
            setDirections={setDirections}
            contract={contract}
            key={index}
          />
        );
      });
    }
  };

  const handleAmounts = (distance: string, duration: string) => {
    if (currentOrderRoutes.length > 0) {
      const updatedOrderRoute =
        currentOrderRoutes[currentRouteController.index];
      updatedOrderRoute.amounts.distance = distance;
      updatedOrderRoute.amounts.duration = duration;

      setCurrentOrderRoutes(updatedArray(updatedOrderRoute));
    }
  };

  const handlePinAction = (
    invoice: Invoice,
    loading_tag: string,
    removePin: boolean,
    seladBase: boolean,
  ) => {
    const customInvoice: { [key: string]: any } = invoice;
    customInvoice['loading_tag'] = loading_tag;

    const invoiceWithLoadingTag = customInvoice;

    return removePin
      ? removeRoutePoint(invoiceWithLoadingTag as Invoice, seladBase)
      : addRoutePoint(invoiceWithLoadingTag as Invoice);
  };

  const addRoutePoint = (invoice: Invoice) => {
    if (currentOrderRoutes.length > 0) {
      const updatedOrderRoute =
        currentOrderRoutes[currentRouteController.index];
      updatedOrderRoute.invoices.push(invoice);

      updatedOrderRoute.amounts.cubage =
        updatedOrderRoute.amounts.cubage + parseInt(invoice.cubage);

      updatedOrderRoute.amounts.services =
        updatedOrderRoute.amounts.services + invoice.amount_boxes;

      setCurrentOrderRoutes(updatedArray(updatedOrderRoute));

      return currentRouteController;
    }
  };

  const removeRoutePoint = (invoice: Invoice, seladBase: boolean) => {
    if (seladBase) {
      const updatedOrderRoute =
        currentOrderRoutes[currentRouteController.index];
      updatedOrderRoute.invoices = removeFromLoadingRoute(invoice);

      setCurrentOrderRoutes(updatedArray(updatedOrderRoute));

      return;
    }

    if (!seladBase) {
      const teste = currentOrderRoutes.map((item, index) => {
        const find = item.invoices.some((invoiceItem) =>
          invoiceItem.id.includes(invoice.id),
        );

        if (find) {
          const updatedOrderRoute = item;
          updatedOrderRoute.amounts.cubage =
            item.amounts.cubage - parseInt(invoice.cubage);

          updatedOrderRoute.amounts.services =
            item.amounts.services - invoice.amount_boxes;

          updatedOrderRoute.invoices = removeFromLoadingRoute(invoice, index);

          setCurrentOrderRoutes(updatedArray(updatedOrderRoute, index));
        }

        return;
      });
    }

    return null;
  };

  const updatedArray = (
    updatedRoute: RouteData,
    currentIndex = currentRouteController.index,
  ) => {
    const updated = currentOrderRoutes.map((item, index) => {
      return index === currentIndex ? (item = updatedRoute) : item;
    });

    return updated;
  };

  const removeFromLoadingRoute = (
    invoice: Invoice,
    currentIndex = currentRouteController.index,
  ) => {
    const removedFromArray = currentOrderRoutes[currentIndex].invoices.filter(
      (item) => item.id !== invoice.id,
    );

    return removedFromArray;
  };

  return (
    <>
      <Header />

      <Sidebar contract={contract} />

      {!loading ? (
        <Map
          contract={contract}
          directions={directions}
          eraseData={eraseData}
          fetchInvoicesInArea={fetchInvoicesInArea}
          availableInvoices={availableInvoices}
          previousRoutes={previousRoutes}
          handlePinAction={handlePinAction}
          handleAmounts={handleAmounts}
        />
      ) : (
        <Loader />
      )}

      <RouteContainer>
        <RouteContraction onClick={() => setContract(!contract)}>
          {contract ? (
            <>
              <div className="syncAlter">
                <div>Carregamento</div>
              </div>
              <FaAngleDown color="#fff" size="20" />
              <div />
            </>
          ) : (
            <>
              <div className="syncAlter">
                <div>Carregamento</div>
              </div>
              <FaAngleUp color="#fff" size="20" />
              <div />
            </>
          )}
        </RouteContraction>
        <RouteInner>
          {renderPreviousOrderRoutesScripting()}
          {renderCurrentOrderRoutesScripting()}

          <NewRouteContainer contract={contract}>
            <NewRouteButton onClick={() => addNewOrder()}>
              Adicionar Rota
            </NewRouteButton>
          </NewRouteContainer>
        </RouteInner>
      </RouteContainer>
    </>
  );
};
