import React, { useEffect, useState } from 'react';

import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { Select } from 'antd';

import seladHorseImg from '../../../../assets/map/seladHorse.png';

import {
  FaAngleUp,
  FaAngleDown,
  FaAngleRight,
  FaAngleLeft,
  FaRedoAlt,
  FaPen,
} from 'react-icons/fa';

import {
  RouteContainer,
  RouteContraction,
  RouteContent,
  Table,
  Routes,
  RoutesNumbersContainer,
  FinalizeRoute,
  RoutesNumbersDrag,
  RouteStops,
  ActionButtonsWrapper,
  ScriptWrapper,
  TagColor,
} from './styles';
import { toast } from 'react-toastify';
import api from '../../../../services/api';

import {
  Invoice,
  RouteAmounts,
  Truck,
  Trucks,
} from '../../../../Interfaces/MapInterface';

interface RouteScripting {
  routes: Invoice[];
  waypointTrigger?: boolean;
  setWaypointTrigger: Function;
  setDirections: Function;
  contract: boolean;
  color: string;
  truck: Truck;
}

export const PreviousRouteScpripting: React.FC<RouteScripting> = ({
  routes,
  waypointTrigger,
  setDirections,
  contract,
  color,
  truck,
}) => {
  const [contractRoutes, setContractRoutes] = useState(false);

  const [loadingSave, setLoadingSave] = useState<boolean>(false);

  useEffect(() => {
    routes.length > 1 && handleDirection();
  }, [waypointTrigger]);

  const renderRoutePoints = () => {
    return (
      routes.length > 0 &&
      routes.map((item, index) => {
        return (
          <span className="partTwoNumbers">
            <p>{index + 1}</p>
          </span>
        );
      })
    );
  };

  const handleDirection = () => {
    let start = `${routes[0].latitude + ',' + routes[0].longitude}`;

    let end = `${
      routes[routes.length - 1].latitude +
      ',' +
      routes[routes.length - 1].longitude
    }`;

    let waypoints: any[] = [];

    routes.forEach((item, index) => {
      if (index !== 0 && index !== routes.length - 1) {
        const coordinates = {
          location: `${item.latitude + ',' + item.longitude}`,
        };
        waypoints.push(coordinates);
      }
    });

    let request = {
      origin: start,
      destination: end,
      travelMode: 'DRIVING',
      waypoints: waypoints,
    };

    setDirections(request);
  };

  return (
    <>
      <ScriptWrapper>
        <div className="routeTable">
          <RouteContent>
            {contract ? (
              <Table>
                <thead>
                  <tr>
                    <th>Rota</th>

                    <th>Veculo</th>

                    <th>
                      Servios
                      <br />
                    </th>

                    <th>
                      Distncia (Km) <br />
                    </th>

                    <th>
                      Tempo(h) <br />
                    </th>

                    <th>
                      Peso(kg) <br />
                    </th>

                    <th>
                      Volume(m) <br />
                    </th>
                  </tr>
                </thead>

                <tbody>
                  <tr>
                    <TagColor
                      data-label={`Rotas(${routes.length})`}
                      color={color}
                    >
                      <div>
                        <span />

                        <div></div>
                      </div>
                    </TagColor>

                    <td data-label="Veculo">{truck.plate}</td>

                    <td data-label="Servios">
                      <span>12</span>
                    </td>

                    <td data-label="Distncia (Km)">584,7</td>

                    <td data-label="Tempo(h)">6h 48m</td>

                    <td data-label="Peso">300</td>

                    <td data-label="Volume(m)">9,43</td>
                  </tr>
                </tbody>
              </Table>
            ) : (
              ''
            )}
          </RouteContent>
        </div>

        {contract ? (
          <Routes contractRoutes={contractRoutes}>
            <RoutesNumbersContainer>
              {/* <div
                  className="routesContract"
                  onClick={() => setContractRoutes(!contractRoutes)}
                >
                  {contractRoutes ? <FaAngleLeft /> : <FaAngleRight />}
                </div> */}

              {!contractRoutes ? (
                <div className="routesNumbersDragDrop">
                  <FinalizeRoute>
                    {/* <h3>Finalizar rota:</h3> */}

                    <button
                      disabled={routes.length <= 1 && true}
                      onClick={() => handleDirection()}
                      style={{
                        backgroundColor: 'var(--blue)',
                        margin: '0 5px',
                      }}
                    >
                      Visualizar Rota
                    </button>

                    {/* <button disabled>
                      {loadingSave ? 'Salvando' : 'Salvar'}
                    </button> */}
                  </FinalizeRoute>

                  <RoutesNumbersDrag color={color}>
                    <div>
                      <span className="partOne">
                        <p>Rota</p>
                      </span>

                      <span className="partTwo">{renderRoutePoints()}</span>

                      <span className="partThree">
                        <img src={seladHorseImg} />
                      </span>
                    </div>
                  </RoutesNumbersDrag>
                </div>
              ) : (
                ''
              )}
            </RoutesNumbersContainer>
          </Routes>
        ) : (
          ''
        )}
      </ScriptWrapper>

      {contract ? (
        <RouteStops>
          <div>
            {/* <span>
                12 paradas - 10/02/2020 0.... <FaPen color="#0440A0" />
              </span>

              <span>15/01/2021 16:16 </span>

              <span>Atribudos: 9/12</span>

              <span>Rotas: 3</span>

              <span>Distncia: 741,99km</span> */}
          </div>
        </RouteStops>
      ) : (
        ''
      )}
    </>
  );
};
