import { useEffect, useState } from 'react';

import ModalFreeze from '../ModalFreeze/index';
import ModalLoad from '../ModalLoad/index';

import boxImg from './assets/box.png';
import clockImg from './assets/clock.png';
import fileImg from './assets/file.png';
import markImg from './assets/mark.png';
import timeImg from './assets/time.png';
import handIcon from './assets/handIcon.png';
import loadIcon from './assets/loadIcon.png';
import positionIcon from './assets/positionIcon.png';
import seladHorse from '../../../../assets/map/seladHorse.png';

import {
  MarkerWrapper,
  Marker,
  Number,
  Info,
  HorseIcon,
} from './_LocationMarker';

import { Invoice, RouteController } from '../../../../Interfaces/MapInterface';

interface Marker {
  lat?: number;
  lng?: number;
  color?: string;
  order?: number;
  invoiceInfo: Invoice;
  handlePinAction?: Function;
  seladBase: boolean;
}

export const LocationMarker: React.FC<Marker> = ({
  invoiceInfo,
  order,
  handlePinAction,
  color = 'gray',
  seladBase,
}) => {
  const emptyControler = {
    index: null,
    color: '',
  };
  const [showModalFreeze, setShowModalFreeze] = useState<boolean>(false);
  const [showModalLoad, setShowModalLoad] = useState<boolean>(false);

  const baseColor = 'var(--blue)';

  const [newColor, setNewColor] = useState<string>();

  const [loadingRouteData, setLoadingRouteData] = useState<
    RouteController | undefined
  >();

  const [ableToRemove, setAbleToRemove] = useState<boolean>(false);

  useEffect(() => {
    !loadingRouteData ? resetConfig() : setNewColor(loadingRouteData?.color);
  }, [loadingRouteData]);

  const resetConfig = () => {
    setNewColor(undefined);
    setAbleToRemove(false);
  };

  const handleClick = () => {
    if (handlePinAction) {
      setLoadingRouteData(
        handlePinAction(invoiceInfo, String(order), ableToRemove, seladBase),
      );

      setAbleToRemove(true);
    }
  };

  const elementColor = () => {
    if (seladBase) {
      return baseColor;
    }

    if (!seladBase) {
      return newColor || color;
    }
  };

  return (
    <MarkerWrapper>
      <Info>
        <div
          style={{
            width: '30%',
            background: `${elementColor()}`,
            borderRadius: '20px',
            padding: '4px 13px 4px 13px',
            textAlign: 'center',
            marginBottom: '10px',
          }}
        >
          Rota
        </div>

        <h4>
          <strong>{invoiceInfo.client_name}</strong>
        </h4>

        <ul>
          <li>
            <img style={{ marginRight: '10px' }} width="15px" src={fileImg} />
            <strong>Nmero NF:</strong> {invoiceInfo.invoice_number}
          </li>

          <li>
            <img style={{ marginRight: '10px' }} width="15px" src={markImg} />
            {invoiceInfo.address}
          </li>

          <li>
            <img style={{ marginRight: '10px' }} width="15px" src={clockImg} />
            <strong>CEP:</strong> {invoiceInfo.cep}
          </li>

          <li>
            <img style={{ marginRight: '10px' }} width="15px" src={boxImg} />
            <strong>Caixas:</strong> {invoiceInfo.amount_boxes}
          </li>

          <li>
            <img style={{ marginRight: '10px' }} width="15px" src={timeImg} />
            <strong>Cubagem:</strong> {invoiceInfo.cubage} m
          </li>

          {/* <li>
              <img
                style={{ marginRight: '10px' }}
                width="15px"
                src={positionIcon}
              />
              <strong>Posies:</strong> 14-06-0000
            </li> */}

          {/* <li className="actionButtons">
              <div
                className="anchor__freeze"
                onClick={() => setShowModalFreeze(true)}
              >
                <img src={handIcon} style={{ marginRight: '12px' }} />
                Congelar
              </div>

              <div
                className="anchor__confirm-load"
                onClick={() => setShowModalLoad(true)}
              >
                <img src={loadIcon} style={{ marginRight: '12px' }} />
                Carregada
              </div>
            </li> */}
        </ul>
      </Info>
      <Marker tagColor={`${elementColor()}`} onClick={() => handleClick()}>
        {seladBase ? (
          <HorseIcon src={seladHorse} alt="Sede Selad" />
        ) : (
          <Number tagColor={`${elementColor()}`}>{order}</Number>
        )}
      </Marker>

      <ModalFreeze
        setOpenModal={setShowModalFreeze}
        openModal={showModalFreeze}
      />
      <ModalLoad setOpenModal={setShowModalLoad} openModal={showModalLoad} />
    </MarkerWrapper>
  );
};
