import Modal from 'react-modal';
import React, { useState, Dispatch, SetStateAction } from 'react';
import { Select } from 'antd';
import { HiBadgeCheck } from 'react-icons/hi';

import './modal-responsive.css';

import {
  Content,
  SelectLabel,
  Label,
  Input,
  ButtonsWrapper,
  MainButtons,
  SaveButton,
  BackButton,
} from './styles';

interface Props {
  openModal: boolean;
  setOpenModal: Dispatch<SetStateAction<boolean>>;
}

const ModalLoad = ({ openModal, setOpenModal }: Props) => {
  const { Option } = Select;

  const [pendingNotes, setPendingNotes] = useState(false);

  function renderContent() {
    if (pendingNotes) {
      return (
        <>
          <h1>Ainda existem notas pendentes</h1>
          <p>
            Alguma(s) nota(s) neste carregamento ainda no foram marcadas como
            congelada ou separada.
          </p>

          <p>
            Caso voc aprove este carregamento, todas essas notas restantes
            sero marcadas como separada.
          </p>
        </>
      );
    } else {
      return (
        <>
          <h1>Nota carregada</h1>

          <h4>Notas de alterao</h4>
          <SelectLabel>
            Marcador de status (opcional)
            <Select placeholder="Selecione...." bordered={false}>
              <Option value="">Cubagem</Option>
              <Option value="">Imposto</Option>
              <Option value="">Carga fracionada sp</Option>
              <Option value="">Retorno de NF</Option>
              <Option value="">Agendamento</Option>
            </Select>
          </SelectLabel>
          <Label>
            Comentrio (opcional)
            <Input value="" />
          </Label>
        </>
      );
    }
  }

  return (
    <Modal
      isOpen={openModal}
      onRequestClose={() => setOpenModal(false)}
      style={{
        overlay: { backgroundColor: 'rgba(0,0,0, 0)', zIndex: 1000 },
      }}
      className="modal-responsive-freeze"
    >
      <Content>
        {renderContent()}
        <ButtonsWrapper>
          <MainButtons>
            <SaveButton onClick={() => setPendingNotes(!pendingNotes)}>
              <HiBadgeCheck size={24} />
              Confirmar
            </SaveButton>
            <BackButton onClick={() => setOpenModal(false)}>
              Cancelar
            </BackButton>
          </MainButtons>
        </ButtonsWrapper>
      </Content>
    </Modal>
  );
};

export default ModalLoad;
