import React, { useEffect } from 'react';
import { useState } from 'react';
import GoogleMapReact from 'google-map-react';

import { ActionButtonsWrapper, MapContainer, ResetButton } from './_Map';
import { LocationMarker } from '../LocationMarker/LocationMarker';

import {
  Area,
  Invoice,
  PreviousRoutes,
} from '../../../../Interfaces/MapInterface';
import { color } from '../../../../utils/colors';

interface Map {
  center?: any;
  zoom?: any;
  contract: boolean;
  fetchInvoicesInArea: Function;
  availableInvoices: Invoice[];
  eraseData: Function;
  directions: any;
  previousRoutes?: PreviousRoutes[];
  handlePinAction: Function;
  handleAmounts: Function;
}

const Map: React.FC<Map> = ({
  center,
  zoom,
  contract,
  eraseData,
  fetchInvoicesInArea,
  availableInvoices,
  directions,
  previousRoutes,
  handlePinAction,
  handleAmounts,
}) => {
  const [mapLoaded, setMapLoaded] = useState<boolean>(false);
  const seladData = {
    id: 'Selad Transportes',
    invoice_number: '--',
    order_number: '--',
    client_name: 'Selad Transportes',
    cubage: '0',
    amount_boxes: 0,
    invoice_was_received: false,
    cep: '--',
    address: '--',
    longitude: -46.44126057503965,
    latitude: -23.563803174216396,
    status: '--',
    was_delivered: false,
    position_occupied: false,
    delivery_order: 0,
    user_id: '--',
    invoice_route_id: null,
    created_at: '--',
    updated_at: '--',
  };

  const [area, setArea] = useState<any[]>();
  const [draw, setDraw] = useState<any>();
  const [shape, setShape] = useState<any>();

  const [drawPolygon, setDrawPolygon] = useState<any>();
  const [map, setMap] = useState<any>();

  const [maps, setMaps] = useState<any>();

  const [directionsRenderer, setDirectionsRenderer] = useState<any>();
  const [directionsService, setDirectionsService] = useState<any>();

  useEffect(() => {
    maps && directions && calcRoute();
  }, [directions]);

  const handleApiLoaded = (map: any, maps: any) => {
    setTimeout(() => {
      setMapLoaded(true);
      setMaps(maps);
      setMap(map);

      // createDrawer(maps, map);
    }, 500);
  };

  const renderSeladPin = () => {
    return (
      <LocationMarker
        lat={-23.563803174216396}
        lng={-46.44126057503965}
        invoiceInfo={seladData}
        order={0}
        seladBase={true}
        color={'var(--blue)'}
        handlePinAction={handlePinAction}
      />
    );
  };

  const renderAvailableInvoicesPin = () => {
    return availableInvoices.map((invoice, index) => {
      return (
        <LocationMarker
          key={index}
          lat={invoice.latitude}
          lng={invoice.longitude}
          invoiceInfo={invoice}
          order={index}
          color={'gray'}
          handlePinAction={handlePinAction}
          seladBase={false}
        />
      );
    });
  };

  const renderPreviousOrderInvoicesPin = () => {
    if (previousRoutes) {
      return previousRoutes.map(({ invoices }, index) => {
        return renderInvoices(invoices, color(index));
      });
    }

    function renderInvoices(invoices: Invoice[], color: string) {
      return invoices.map((invoice, index) => {
        return (
          <LocationMarker
            key={index}
            lat={invoice.latitude}
            lng={invoice.longitude}
            invoiceInfo={invoice}
            order={index + 1}
            color={color}
            seladBase={false}
          />
        );
      });
    }
  };

  const createDrawer = async (maps: any, map: any) => {
    let drawingPolygon = await new maps.drawing.DrawingManager({
      drawingControl: true,
      drawingControlOptions: {
        position: maps.ControlPosition.TOP_CENTER,
        drawingModes: [maps.drawing.OverlayType.POLYGON],
      },
      polygonOptions: {
        strokeColor: '#002e75',
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillColor: '#ff9700',
        fillOpacity: 0.25,
      },
    });

    if (drawingPolygon) {
      drawingPolygon.setMap(map);

      drawingPolygon.addListener('polygoncomplete', (polygon: any) => {
        let polygonBounds = polygon.getPath();

        let coordinates = [];

        for (let i = 0; i < polygonBounds.length; i++) {
          let point = {
            lat: polygonBounds.getAt(i).lat(),
            lng: polygonBounds.getAt(i).lng(),
          };
          coordinates.push(point);
        }

        handleCoordinates(coordinates);

        setDraw(drawingPolygon);
        setShape(polygon);

        drawingPolygon.setMap(null);
        setDrawPolygon(drawingPolygon);
        setMap(map);
      });
    }
  };

  const resetPolygon = () => {
    draw.setDrawingMode(null);
    shape.setMap(null);

    setArea(undefined);
    drawPolygon.setMap(map);

    eraseData();
  };

  const erasePolygon = () => {
    draw.setDrawingMode(null);
    shape.setMap(null);

    setArea(undefined);
    drawPolygon.setMap(map);
  };

  const handleCoordinates = (area: Area[]) => {
    const refactoredArray: any[] = [];

    area.map(({ lat, lng }) => {
      refactoredArray.push([lng, lat]);
    });

    setArea(refactoredArray);

    fetchInvoicesInArea(refactoredArray);
  };

  const calcRoute = () => {
    resetRoute();

    let directionsService = new maps.DirectionsService();
    let directionsRenderer = new maps.DirectionsRenderer();

    directionsService.route(directions, function (result: any, status: any) {
      if (status == 'OK') {
        directionsRenderer.setDirections(result);
        getDistanceAndTime(result.routes[0].legs);
      }
    });

    directionsRenderer.setMap(map);

    setDirectionsRenderer(directionsRenderer);
    setDirectionsService(directionsService);

    // erasePolygon();
  };

  const getDistanceAndTime = (legs: any) => {
    let distance: number = 0;
    let duration: number = 0;

    legs.map((item: any) => {
      distance = distance + item.distance.value;

      duration = duration + item.duration.value;
    });

    handleAmounts(String(distance / 1000), String(minutesToHours(duration)));
  };

  const minutesToHours = (time: number) => {
    let h = Math.floor(time / 3600);
    let m = Math.floor((time % 3600) / 60);

    let hour = h + 'h';
    let minute = m + 'm';
    return hour + minute;
  };

  const resetRoute = () => {
    if (directionsRenderer) {
      directionsRenderer.setDirections(null);

      directionsRenderer.setMap(null);
    }
  };

  const bootstrapURLKeys = {
    key: 'AIzaSyDOM3_4X7fai3TdgMZeYmZ3irA5eX9qiXY',
    libraries: ['drawing'].join(','),
  };

  return (
    <MapContainer contract={contract}>
      <ActionButtonsWrapper>
        {area && (
          <ResetButton onClick={() => resetPolygon()}>Apagar rea</ResetButton>
        )}
      </ActionButtonsWrapper>

      <GoogleMapReact
        bootstrapURLKeys={bootstrapURLKeys}
        defaultCenter={center}
        defaultZoom={zoom}
        yesIWantToUseGoogleMapApiInternals
        onGoogleApiLoaded={({ map, maps }) => handleApiLoaded(map, maps)}
      >
        {renderSeladPin()}
        {renderAvailableInvoicesPin()}
        {previousRoutes && renderPreviousOrderInvoicesPin()}
      </GoogleMapReact>
    </MapContainer>
  );
};

Map.defaultProps = {
  center: {
    lat: -23.5638599,
    lng: -46.4412612,
  },
  zoom: 6,
};

export default Map;
