import { useState } from 'react';

import api from '../../../../services/api';
import { toast } from 'react-toastify';

import { Spinner } from '../../../../components/Spinner/Spinner';
import { HiBadgeCheck } from 'react-icons/hi';

import {
  Container,
  SearchWrapper,
  SearchBoxContainer,
  SearchBox,
  SearchButton,
  InputContainer,
  ConfirmButton,
} from './styles';

interface Invoice {
  invoice_number: string;
  client_name: string;
  city: string;
  cep?: string;
  weight: number;
  cubage: number;
  neighborhood?: string;
  number?: string;
  status?: string;
  street?: string;
  uf?: string;
}

interface OrderFromProps {
  setStep: (step: number) => void;
  invoiceID: string;
  invoiceCubage: number;
}

export const Confirm: React.FC<OrderFromProps> = ({
  setStep,
  invoiceID,
  invoiceCubage,
}) => {
  const emptyInvoiceData = {
    invoice_number: '',
    client_name: '',
    city: '',
    cep: '',
    weight: 0,
    cubage: 0,
    neighborhood: '',
    number: '',
    status: 'No autorizado',
    street: '',
    uf: '',
  };

  const [loading, setLoading] = useState<boolean>(false);
  const [invoiceWasReceipt, setInvoiceWasReceipt] = useState<boolean>(false);
  const [invoiceData, setInvoiceData] = useState<Invoice>(emptyInvoiceData);

  const [invoiceNumber, setInvoiceNumber] = useState<string>();
  const [hasSearchForInvoice, setHasSearchForInvoice] =
    useState<boolean>(false);

  const handleError = async (error: string) => {
    toast.error(error);
  };

  const renderFields = () => {
    if (invoiceData) {
      return (
        <InputContainer>
          <div>
            <label>Nmero da Nota</label>
            <input
              onChange={(e) =>
                setInvoiceData({
                  ...invoiceData,
                  invoice_number: e.target.value,
                })
              }
              defaultValue={invoiceData && invoiceData.invoice_number}
            />
          </div>

          <div>
            <label>Cliente</label>
            <input
              onChange={(e) =>
                setInvoiceData({ ...invoiceData, client_name: e.target.value })
              }
              defaultValue={invoiceData && invoiceData.client_name}
            />
          </div>

          <div>
            <label>Endereo</label>
            <input
              value={invoiceData.street}
              onChange={(e) =>
                setInvoiceData({ ...invoiceData, street: e.target.value })
              }
            />
          </div>

          <div>
            <label>Nmero</label>
            <input
              type="number"
              value={invoiceData.number}
              onChange={(e) =>
                setInvoiceData({ ...invoiceData, number: e.target.value })
              }
            />
          </div>

          <div>
            <label>Bairro</label>
            <input
              value={invoiceData.neighborhood}
              onChange={(e) =>
                setInvoiceData({ ...invoiceData, neighborhood: e.target.value })
              }
            />
          </div>

          <div>
            <label>Cidade</label>
            <input
              defaultValue={invoiceData && invoiceData.city}
              onChange={(e) =>
                setInvoiceData({ ...invoiceData, city: e.target.value })
              }
            />
          </div>

          <div>
            <label>Estado</label>
            <input
              onChange={(e) =>
                setInvoiceData({ ...invoiceData, uf: e.target.value })
              }
              defaultValue={invoiceData && invoiceData.uf}
            />
          </div>

          <div>
            <label>CEP</label>
            <input
              value={invoiceData.cep}
              onChange={(e) =>
                setInvoiceData({
                  ...invoiceData,
                  cep: sanitize(e.target.value),
                })
              }
            />
          </div>

          {/* <div>
            <label>Peso</label>
            <input
              onChange={(e) =>
                setInvoiceData({
                  ...invoiceData,
                  weight: Number(e.target.value),
                })
              }
              defaultValue={invoiceData && invoiceData.weight}
            />
          </div> */}
        </InputContainer>
      );
    }
  };

  const handleConfirmInvoice = async (e: any) => {
    e.preventDefault();
    setLoading(true);

    const data = {
      cep: invoiceData.cep,
      city: invoiceData.city,
      client_name: invoiceData.client_name,
      cubage: invoiceData.cubage,
      invoice_number: invoiceData.invoice_number,
      neighborhood: invoiceData.neighborhood,
      number: invoiceData.number,
      status: invoiceData.status,
      street: invoiceData.street,
      uf: invoiceData.uf,
    };

    try {
      const response = await api.post(`invoices/${invoiceID}/confirm`, data);

      if (response) {
        toast.success('Nota confirmada com sucesso!', {
          autoClose: 3000,
          onClose: () => setStep(3),
        });
      }
    } catch (error: any) {
      handleError(error.response?.data?.message || error.toString());
    } finally {
      setLoading(false);
    }
  };

  const renderConfirmation = () => {
    if (loading) {
      return (
        <p>
          <Spinner />
        </p>
      );
    }

    if (invoiceWasReceipt) {
      return (
        <>
          {renderFields()}

          <ConfirmButton
            disabled={disableButton()}
            onClick={(e) => handleConfirmInvoice(e)}
          >
            <HiBadgeCheck />
            Confirmar
          </ConfirmButton>
        </>
      );
    }

    if (!invoiceWasReceipt && hasSearchForInvoice) {
      return (
        <>
          <p>Esta nota ainda no foi recebida.</p>

          {/* <p>Informe os dados abaixo para confirmar a nota</p> */}
        </>
      );
    }
  };

  const disableButton = () => {
    if (
      invoiceData.invoice_number !== '' &&
      invoiceData.client_name !== '' &&
      invoiceData.city !== '' &&
      invoiceData.cep !== '' &&
      invoiceData.neighborhood !== '' &&
      invoiceData.number !== '' &&
      invoiceData.status !== '' &&
      invoiceData.street !== '' &&
      invoiceData.uf !== ''
    ) {
      return false;
    }

    return true;
  };

  const fetchInvoice = async () => {
    setLoading(true);
    setHasSearchForInvoice(true);
    setInvoiceWasReceipt(false);
    setInvoiceData(emptyInvoiceData);

    try {
      const response = await api.get(`invoices/${invoiceNumber}/bsoft`);

      if (response) {
        if (response.data.status !== 'error') {
          setInvoiceData({
            ...invoiceData,
            invoice_number: String(response.data.numero),
            client_name: response.data.destinatario_nome,
            city: response.data.destinatario_municipio,
            weight: Number(response.data.peso),
            uf: response.data.destinatario_uf,
            status: response.data.situacao,
            cubage: invoiceCubage,
          });

          setInvoiceWasReceipt(true);
        }
      }
    } catch (error: any) {
      handleError(error.response?.data?.message || error.toString());
    } finally {
      setLoading(false);
    }
  };

  const handleSearch = (e: any) => {
    e.preventDefault();

    fetchInvoice();
  };

  const sanitize = (value: string) => {
    let sanitized = value.replace(/\D/g, '');

    return sanitized;
  };

  return (
    <>
      <Container>
        <SearchWrapper>
          <SearchBoxContainer>
            <SearchBox
              placeholder="Digite o nmero da nota"
              onChange={(e) => setInvoiceNumber(e.target.value)}
            />
          </SearchBoxContainer>

          <SearchButton
            disabled={!invoiceNumber || loading}
            onClick={(e) => handleSearch(e)}
          >
            Buscar
          </SearchButton>
        </SearchWrapper>
        {renderConfirmation()}
      </Container>
    </>
  );
};
