import { useEffect, useState } from 'react';

import { FaPlus, FaSearch } from 'react-icons/fa';
import { HiBadgeCheck } from 'react-icons/hi';
import { FaPlusCircle } from 'react-icons/fa';
import { Input } from '../Input/Input';
import { Radio, Tabs } from 'antd';
import { Select } from 'antd';

import { toast } from 'react-toastify';

import api from '../../../../services/api';

import MapModal from '../Modal/index';

import routeImg from '../../../../assets/map/route.png';
import markImg from '../../../../assets/map/mark.png';
import editImg from '../../../../assets/map/editIcon.png';
import houseImg from '../../../../assets/map/house.png';
import rightArrowImg from '../../../../assets/map/rightArrow.png';
import circleImg from '../../../../assets/map/circle.png';
import truckImg from '../../../../assets/map/truck.png';
import boxImg from '../../../../assets/map/box.png';
import kgImg from '../../../../assets/map/kg.jpg';
import calendarImg from '../../../../assets/map/calendar.png';
import m3Img from '../../../../assets/map/m3.png';
import timeImg from '../../../../assets/map/time.png';
import fileImg from '../../../../assets/map/file.png';
import mapImg from '../../../../assets/map/map.png';

import { Sidebar as Sidenav } from '../../../../components/Sidebar';

import {
  Container,
  ModalContent,
  InputForm,
  RouteForm,
  Label,
  PointOfOrigin,
  Routing,
  AddRoute,
  SaveButton,
  Localization,
  StreetInfoOne,
  StreetInfoTwo,
  StreetInfoThree,
  Client,
  ClientInfoOne,
  Service,
  SelectLabel,
  EditRoute,
  NoteCotainer,
  SidenavContainer,
} from './styles';

const theme = {
  overflowY: 'scroll',
  height: true,
};

const { TabPane } = Tabs;

interface Route {
  name: string;
  zipcode: string;
  address: string;
  address_number: string;
  address_complement: string;
  city: string;
  neighboorhood: string;
  state: string;
  customer_name: string;
  phone: string;
  email: string;
  service_type: string;
  weight: string;
  cubage: string;
  start_hour: string;
  final_hour: string;
  service_duration: string;
  invoice_number: string;
  order_number: string;
  truck: string;
  area: string;
}

interface Truck {
  name: string;
  id: string;
}

interface SidebarProps extends React.HTMLAttributes<HTMLElement> {
  contract: boolean;
}

export const Sidebar = ({ contract, ...rest }: SidebarProps) => {
  const [showModal, setShowModal] = useState<boolean>(false);
  const [menu, setMenu] = useState<string>('menu');
  const [value, setValue] = useState(1);
  const [searchInput, setSearchInput] = useState(false);
  const { Option } = Select;
  const emptyRoute = {
    name: '',
    zipcode: '',
    address: '',
    address_number: '',
    address_complement: '',
    city: '',
    neighboorhood: '',
    state: '',
    customer_name: '',
    phone: '',
    email: '',
    service_type: '',
    weight: '',
    cubage: '',
    start_hour: '',
    final_hour: '',
    service_duration: '',
    invoice_number: '',
    order_number: '',
    truck: '',
    area: '',
  };

  const [loading, setLoading] = useState<boolean>(false);

  const [roadMappingName, setRoadMappingName] = useState<string>('');

  const [trucks, setTrucks] = useState<Truck[]>();

  const [currentRoute, setCurrentRoute] = useState<Route>({
    name: '',
    zipcode: '',
    address: '',
    address_number: '',
    address_complement: '',
    city: '',
    neighboorhood: '',
    state: '',
    customer_name: '',
    phone: '',
    email: '',
    service_type: '',
    weight: '',
    cubage: '',
    start_hour: '',
    final_hour: '',
    service_duration: '',
    invoice_number: '',
    order_number: '',
    truck: '',
    area: '',
  });

  const [routes, setRoutes] = useState<Route[]>([]);

  const [newRouteName, setNewRouteName] = useState<string>('');
  const [addingNewRoute, setAddingNewRoute] = useState<boolean>(false);

  useEffect(() => {
    fetchTrucks();
  }, []);

  const fetchTrucks = async () => {
    setLoading(true);
    try {
      const response = await api.get(`trucks?limit=99999`);

      if (response) {
        setTrucks(response.data.results);
      }
    } catch (error: any) {
      handleError(error.response?.data?.message || error.toString());
    } finally {
      setLoading(false);
    }
  };

  const handleError = async (error: string) => {
    toast.error(error);
  };

  const onChange = (e: any) => {
    setValue(e.target.value);
  };

  const handleServiceType = (e: any) => {
    setCurrentRoute((currentRoute) => ({
      ...currentRoute,
      service_type: e.target.value,
    }));
  };

  const renderTruckOptions = () => {
    return (
      trucks &&
      trucks.map(({ name, id }, index) => {
        return (
          <Option value={name} key={index}>
            {name}
          </Option>
        );
      })
    );
  };

  const handleChangeSelection = (value: any) => {
    console.log(`selected ${value}`);
  };

  const handleSaveNewRoute = () => {
    setRoutes((routes) => [...routes, currentRoute]);

    setCurrentRoute(emptyRoute);

    setAddingNewRoute(false);
  };

  const handleAddNewRouteEntry = () => {
    setCurrentRoute(emptyRoute);

    setAddingNewRoute(true);
  };

  const handleAddNewRoute = () => {
    setCurrentRoute(emptyRoute);

    setCurrentRoute((currentRoute) => ({
      ...currentRoute,
      name: newRouteName,
    }));

    setMenu('origin');
  };

  const handleEditRouteTitle = (routeName: string, routeIndex: number) => {
    setCurrentRoute(emptyRoute);

    setCurrentRoute((currentRoute) => ({
      ...currentRoute,
      name: routeName,
    }));
  };

  const handleEditRoute = () => {};

  const renderAddedRoutes = () => {
    const addedRoutes = routes.slice(1, routes.length);

    if (addedRoutes.length > 0) {
      return addedRoutes.map(({ name }, index) => {
        return (
          <Label onFocus={() => handleEditRouteTitle(name, index + 1)}>
            <Routing key={index}>
              <img
                src={circleImg}
                height="20px"
                style={{ marginRight: '10px' }}
              />

              <Input
                className="routeInput"
                defaultValue={name}
                onChange={(e) =>
                  setCurrentRoute((currentRoute) => ({
                    ...currentRoute,
                    name: e.target.value,
                  }))
                }
              />

              <img
                src={editImg}
                height="30px"
                style={{ marginLeft: '10px', cursor: 'pointer' }}
                onClick={() => setMenu('origin')}
              />
            </Routing>
          </Label>
        );
      });
    }
  };

  function renderModalOptions() {
    switch (menu) {
      case 'menu':
        return (
          <ModalContent>
            <div
              style={{ cursor: 'pointer' }}
              onClick={() => {
                setMenu('route');
              }}
            >
              <img src={routeImg} height="25px" />

              <p style={{ marginBottom: '20px' }}>Adicionar roteirizao</p>
            </div>

            <div>
              <img
                src={markImg}
                height="19px"
                style={{ paddingRight: '11px' }}
              />
              <p>Adicionar Rota</p>
            </div>
          </ModalContent>
        );

      case 'route':
        return (
          <ModalContent>
            <div>
              <img
                src={routeImg}
                height="30px"
                style={{ marginRight: '10px' }}
              />
              <RouteForm>
                <h2>Adicionar roteirizao</h2>

                <Label>
                  Nome roteirizao
                  <InputForm
                    onChange={(e) => setRoadMappingName(e.target.value)}
                  />
                </Label>

                <Label>
                  Defina um ponto de origem:
                  <PointOfOrigin>
                    <div>
                      <img src={houseImg} height="30px" />

                      <h4>{routes && routes[0]?.customer_name}</h4>
                    </div>

                    <div style={{ marginLeft: '60px' }}>
                      {!routes[0] ? (
                        <button onClick={() => setMenu('origin')}>NOVO</button>
                      ) : (
                        <img
                          onClick={() => setMenu('editRoute')}
                          src={editImg}
                          height="30px"
                          style={{ cursor: 'pointer' }}
                        />
                      )}
                    </div>
                  </PointOfOrigin>
                </Label>

                {routes.length >= 1 && <Label>Incluir nova rota:</Label>}

                {renderAddedRoutes()}

                {addingNewRoute && (
                  <Routing>
                    <img
                      src={circleImg}
                      height="20px"
                      style={{ marginRight: '10px' }}
                    />

                    <Input
                      placeholder="D um nome a sua rota"
                      className="routeInput"
                      onChange={(e) => setNewRouteName(e.target.value)}
                    />

                    <img
                      src={rightArrowImg}
                      height="30px"
                      style={{ cursor: 'pointer' }}
                      onClick={() => handleAddNewRoute()}
                    />
                  </Routing>
                )}

                {routes.length >= 1 && (
                  <AddRoute onClick={() => handleAddNewRouteEntry()}>
                    <FaPlusCircle size={40} />

                    <span>adicionar nova rota</span>
                  </AddRoute>
                )}
              </RouteForm>
            </div>
          </ModalContent>
        );

      case 'origin':
        return (
          <ModalContent>
            <Tabs defaultActiveKey="1" centered>
              <TabPane tab="Localizao" key="1">
                <Localization>
                  <StreetInfoOne>
                    <Label>CEP</Label>
                    <InputForm
                      onChange={(e) =>
                        setCurrentRoute((currentRoute) => ({
                          ...currentRoute,
                          zipcode: e.target.value,
                        }))
                      }
                    />

                    <Label>Logradouro</Label>
                    <InputForm
                      onChange={(e) =>
                        setCurrentRoute((currentRoute) => ({
                          ...currentRoute,
                          address: e.target.value,
                        }))
                      }
                    />
                  </StreetInfoOne>

                  <StreetInfoTwo>
                    <div className="first">
                      <Label>Nmero</Label>
                      <InputForm
                        onChange={(e) =>
                          setCurrentRoute((currentRoute) => ({
                            ...currentRoute,
                            address_number: e.target.value,
                          }))
                        }
                      />
                    </div>

                    <div>
                      <Label>Complemento</Label>
                      <InputForm
                        onChange={(e) =>
                          setCurrentRoute((currentRoute) => ({
                            ...currentRoute,
                            address_complement: e.target.value,
                          }))
                        }
                      />
                    </div>
                  </StreetInfoTwo>

                  <StreetInfoTwo>
                    <div className="first">
                      <Label>Municpio</Label>
                      <InputForm
                        onChange={(e) =>
                          setCurrentRoute((currentRoute) => ({
                            ...currentRoute,
                            city: e.target.value,
                          }))
                        }
                      />
                    </div>

                    <div>
                      <Label>Bairro</Label>
                      <InputForm
                        onChange={(e) =>
                          setCurrentRoute((currentRoute) => ({
                            ...currentRoute,
                            neighboorhood: e.target.value,
                          }))
                        }
                      />
                    </div>
                  </StreetInfoTwo>

                  <StreetInfoThree>
                    <div className="first">
                      <Label>Estado</Label>
                      <InputForm
                        onChange={(e) =>
                          setCurrentRoute((currentRoute) => ({
                            ...currentRoute,
                            state: e.target.value,
                          }))
                        }
                      />
                    </div>

                    <div className="button">
                      <SaveButton>
                        <HiBadgeCheck color="#fff" size={33} />
                        Salvar
                      </SaveButton>
                    </div>
                  </StreetInfoThree>
                </Localization>
              </TabPane>

              <TabPane tab="Cliente" key="2">
                <Client>
                  <ClientInfoOne>
                    <Label>Nome do Cliente</Label>
                    <InputForm
                      onChange={(e) =>
                        setCurrentRoute((currentRoute) => ({
                          ...currentRoute,
                          customer_name: e.target.value,
                        }))
                      }
                    />

                    <Label>Telefone</Label>
                    <InputForm
                      onChange={(e) =>
                        setCurrentRoute((currentRoute) => ({
                          ...currentRoute,
                          zipcode: e.target.value,
                        }))
                      }
                    />

                    <Label>E-mail</Label>
                    <InputForm
                      onChange={(e) =>
                        setCurrentRoute((currentRoute) => ({
                          ...currentRoute,
                          email: e.target.value,
                        }))
                      }
                    />
                  </ClientInfoOne>

                  <SaveButton>
                    <HiBadgeCheck color="#fff" size={33} />
                    Salvar
                  </SaveButton>
                </Client>
              </TabPane>

              <TabPane tab="Servio" key="3">
                <Service>
                  <div className="firstLabel">
                    <img src={truckImg} height="25px" />
                    <Label>Qual tipo de servio:</Label>
                  </div>

                  <Radio.Group onChange={handleServiceType} value={value}>
                    <div className="radio">
                      <Radio value={1}>
                        <Label>Entrega</Label>
                      </Radio>
                      <Radio value={2}>
                        <Label>Coleta</Label>
                      </Radio>
                    </div>
                  </Radio.Group>

                  <div className="servicesInputsContainer">
                    <div className="label">
                      <img src={boxImg} height="25px" />
                      <Label>Dimenses</Label>
                    </div>

                    <div className="inputs">
                      <Input
                        iconRight={kgImg}
                        onChange={(e) =>
                          setCurrentRoute((currentRoute) => ({
                            ...currentRoute,
                            weight: e.target.value,
                          }))
                        }
                      />

                      <Input
                        iconRight={m3Img}
                        onChange={(e) =>
                          setCurrentRoute((currentRoute) => ({
                            ...currentRoute,
                            cubage: e.target.value,
                          }))
                        }
                      />
                    </div>
                  </div>

                  <div className="servicesInputsContainer">
                    <div className="label">
                      <img src={timeImg} height="25px" />
                      <Label>Indique o horrio de operao</Label>
                    </div>

                    <div className="inputs">
                      <Input
                        placeholder="Horrio inicial"
                        iconRight={calendarImg}
                        onChange={(e) =>
                          setCurrentRoute((currentRoute) => ({
                            ...currentRoute,
                            start_hour: e.target.value,
                          }))
                        }
                      />

                      <Input
                        placeholder="Horrio final"
                        iconRight={calendarImg}
                        onChange={(e) =>
                          setCurrentRoute((currentRoute) => ({
                            ...currentRoute,
                            final_hour: e.target.value,
                          }))
                        }
                      />
                    </div>
                  </div>

                  <div className="servicesInputsContainer">
                    <div className="label">
                      <img src={timeImg} height="25px" />
                      <Label>Indique o tempo de atendimento:</Label>
                    </div>

                    <div className="inputs" style={{ alignSelf: 'flex-start' }}>
                      <Input
                        placeholder="20 Min"
                        onChange={(e) =>
                          setCurrentRoute((currentRoute) => ({
                            ...currentRoute,
                            service_duration: e.target.value,
                          }))
                        }
                      />
                    </div>
                  </div>

                  <div className="servicesInputsContainer">
                    <div className="label">
                      <img src={fileImg} height="25px" />
                      <Label>Nmero da nota fiscal</Label>
                    </div>

                    <Input
                      onChange={(e) =>
                        setCurrentRoute((currentRoute) => ({
                          ...currentRoute,
                          invoice_number: e.target.value,
                        }))
                      }
                    />

                    <div className="label">
                      <img src={fileImg} height="25px" />
                      <Label>Nmero do pedido</Label>
                    </div>

                    <Input
                      onChange={(e) =>
                        setCurrentRoute((currentRoute) => ({
                          ...currentRoute,
                          invoice_number: e.target.value,
                        }))
                      }
                    />

                    <div className="label">
                      <img src={truckImg} height="25px" />
                      <Label>Selecione o Caminho:</Label>
                    </div>
                    <SelectLabel>
                      <Select bordered={false} onChange={handleChangeSelection}>
                        <Option value={''}>{''}</Option>
                        {renderTruckOptions()}
                      </Select>
                    </SelectLabel>

                    <div className="label">
                      <img src={mapImg} height="25px" />
                      <Label>Selecione a regio:</Label>
                    </div>
                    <SelectLabel>
                      <Select
                        bordered={false}
                        defaultValue="all-loadings"
                        onChange={handleChangeSelection}
                      >
                        <Option value="all-loadings">-Selecione-</Option>
                        <Option value="pending-frequency">
                          centro - centro  s / santa efig...
                        </Option>
                        <Option value="selected-driver">
                          centro - centro  s / santa efig...
                        </Option>
                        <Option value="selected-user">
                          centro - centro  s / santa efig...
                        </Option>
                      </Select>
                    </SelectLabel>
                  </div>

                  <div className="button">
                    <SaveButton onClick={() => handleSaveNewRoute()}>
                      <HiBadgeCheck color="#fff" size={33} />
                      Salvar
                    </SaveButton>
                  </div>
                </Service>
              </TabPane>
            </Tabs>
          </ModalContent>
        );

      case 'editRoute':
        return (
          <ModalContent>
            <EditRoute>
              <div className="title">
                <h3>Editando</h3>
                <div>Rota 2</div>
              </div>

              <div className="editFormOne">
                <Label>Nome do cliente</Label>
                <Input placeholder="L MOREIRA DESCARTAVEIS - EPP" />
                <Label>Endereo</Label>
                <Input
                  placeholder="ESTRADA DO CORREDOR SAO PAULO SP Brasil"
                  iconRight={editImg}
                />

                <div className="editFormTwo">
                  <div>
                    <div className="inputs">
                      <Label>Nota fiscal</Label>
                      <Input />
                    </div>
                    <div className="inputs">
                      <Label>Pedido</Label>
                      <Input />
                    </div>
                  </div>
                  <div>
                    <div className="inputs">
                      <Label>Peso Kg</Label>
                      <Input />
                    </div>
                    <div className="inputs">
                      <div style={{ display: 'flex' }}>
                        <Label>Volume M</Label>
                        <span
                          style={{ fontSize: '15px', margin: '-1px 30px 0 0' }}
                        >
                          3
                        </span>
                      </div>
                      <Input />
                    </div>
                  </div>
                  <div>
                    <div className="inputs">
                      <Label>Tempo de atendimento</Label>
                      <Input />
                    </div>
                    <div className="inputs" style={{ width: '100%' }}>
                      <Label>Qual tipo de servio:</Label>
                      <Radio.Group onChange={onChange} value={value}>
                        <div className="radio">
                          <Radio value={1}>
                            <Label>Entrega</Label>
                          </Radio>
                          <Radio value={2}>
                            <Label>Coleta</Label>
                          </Radio>
                        </div>
                      </Radio.Group>
                    </div>
                  </div>
                  <div>
                    <SelectLabel>
                      <Select
                        bordered={false}
                        defaultValue="all-loadings"
                        onChange={handleChangeSelection}
                      >
                        <Option value="all-loadings">Incio</Option>
                        <Option value="pending-frequency">
                          Carregamentos com frequncia pendente
                        </Option>
                        <Option value="selected-driver">
                          Carregamentos deste motorista
                        </Option>
                        <Option value="selected-user">
                          Carregamentos criados por este usurio
                        </Option>
                      </Select>
                      <span className="time">7:59 at 12:00</span>
                    </SelectLabel>
                    <SelectLabel>
                      <Select
                        bordered={false}
                        defaultValue="all-loadings"
                        onChange={handleChangeSelection}
                      >
                        <Option value="all-loadings">Fim</Option>
                        <Option value="pending-frequency">
                          Carregamentos com frequncia pendente
                        </Option>
                        <Option value="selected-driver">
                          Carregamentos deste motorista
                        </Option>
                        <Option value="selected-user">
                          Carregamentos criados por este usurio
                        </Option>
                      </Select>
                      <span className="time">13:00 at 18:00</span>
                    </SelectLabel>
                  </div>
                </div>
                <Label>Telefone de contato</Label>
                <Input />
                <Label>E-mail de contato</Label>
                <Input />

                <Label>Notas</Label>
                <Routing>
                  <img
                    src={circleImg}
                    height="20px"
                    style={{ marginRight: '10px' }}
                  />
                  <Input
                    placeholder="Descreva aqui sua observao"
                    iconRight={rightArrowImg}
                  />
                </Routing>
                <AddRoute>
                  <FaPlusCircle size={40} />
                  <span>adicionar uma nota (observao)</span>
                </AddRoute>

                <NoteCotainer>
                  <div>Rota com trnsito, fique atento</div>
                  <SaveButton>Salvar edio</SaveButton>
                </NoteCotainer>
              </div>
            </EditRoute>
          </ModalContent>
        );

      default:
        break;
    }
  }

  function renderSearchInput() {
    if (searchInput) {
      return <input placeholder="Buscar por cliente, veculo, motorista..." />;
    }
  }

  useEffect(() => {
    if (showModal) {
      setMenu('menu');
    }
  }, [showModal]);

  return (
    <>
      <SidenavContainer {...rest}>
        <Sidenav />

        {/* <Container contract={contract}>
          <div>
            <FaPlus
              onClick={() => setShowModal(!showModal)}
              size="20"
              color="#0440a0"
              style={{ marginBottom: '30px', cursor: 'pointer' }}
            />

            <FaSearch
              size="20"
              color="#0440a0"
              style={{ cursor: 'pointer' }}
              onClick={() => setSearchInput(!searchInput)}
            />

            <MapModal
              menu={menu}
              setOpenModal={setShowModal}
              openModal={showModal}
            >
              {renderModalOptions()}
            </MapModal>
          </div>

          {renderSearchInput()}
        </Container> */}
      </SidenavContainer>
    </>
  );
};
